n-width: 768px){.container-md,.container-sm,.container{max-width:720px}}@media(min-width: 992px){.container-lg,.container-md,.container-sm,.container{max-width:960px}}@media(min-width: 1200px){.container-xl,.container-lg,.container-md,.container-sm,.container{max-width:1140px}}@media(min-width: 1400px){.container-xxl,.container-xl,.container-lg,.container-md,.container-sm,.container{max-width:1320px}}body{font-family:"Fira Sans",sans-serif;background:linear-gradient(180deg, #0F5855 43.01%, #1AB373 156.15%);color:#f4f4f4}#root{background-image:url(/lottery_master/static/media/dots.daf6e652.svg);background-repeat:repeat;background-size:cover;background-position:top left}main{display:flex;align-items:center}main .tickets{display:none}main>div{flex:1 1;display:flex;flex-direction:column;align-items:center}main>div:nth-child(1){align-items:flex-end;padding-right:15px}main>div:nth-child(2)>*{margin-bottom:clamp(15px,2vh,2vh)}main>div:nth-child(2) .connect{margin-top:clamp(15px,2vh,2vh)}main>div:nth-child(3){align-items:flex-start;padding-left:20px}@media(max-width: 992px){main{flex-direction:column}main .tickets{display:block;margin-top:clamp(12px,2.5vh,2.5vh)}main>div:nth-child(3){display:none}main>div>*{margin-bottom:30px}}
